import {Component, OnInit} from '@angular/core';
import {ProfileService} from '../shared/profile.service';
import {EventNode} from 'raain-model';
import {FidjStorageNode} from '../shared/fidj-storage.model';
import {Router} from '@angular/router';

@Component({
    selector: 'app-events',
    templateUrl: 'events.page.html',
    styleUrls: ['events.page.scss'],
})
export class EventsPage implements OnInit {
    public sessionData: FidjStorageNode;
    public eventInfoAlreadyViewed: any = {};
    public notifications: EventNode[];

    constructor(
        private router: Router,
        private profile: ProfileService
    ) {}

    async ngOnInit() {
        await this.refresh();
    }

    async clickOnEvent(item: EventNode) {
        item.red = true;
        await this.profile.storeAll();
    }

    async closeInfo(windowName: string) {
        this.eventInfoAlreadyViewed[windowName] = true;
        if (!this.sessionData || !this.sessionData.infos) {
            return;
        }

        this.sessionData.infos.eventInfoAlreadyViewed = this.eventInfoAlreadyViewed;
        await this.profile.storeAll();
    }

    async refresh(event?: any) {
        try {
            this.sessionData = await this.profile.refreshProfile();
            if (this.sessionData.infos.eventInfoAlreadyViewed) {
                this.eventInfoAlreadyViewed = this.sessionData.infos.eventInfoAlreadyViewed;
            }

            const rains = await this.profile.getRains();
            this.notifications = [];
            for (const rain of rains) {
                const notificationNodes = await this.profile.getNotifications(rain.id);
                this.notifications = this.notifications.concat(notificationNodes);
            }
        } catch (e) {
            console.error('refresh pb:', e);
        }
        if (event) {
            event.target.complete();
        }

        if (!this.profile.isLoggedIn()) {
            return this.profile.gotoLout();
        }
    }
}
